import { useEffect, useState } from 'react';
import { BrowserRouter, Route, Routes, useLocation } from 'react-router-dom';
import Exif from '../../../pages/Exif/Exif';
import FixTextReader from '../../../pages/FixTextReader/FixTextReader';
import JoinText from '../../../pages/JoinText/JoinText';
import TestPage from '../../../pages/Test/TestPage';
import Side from '../Side/Side';
import style from './Style.module.css';

interface MainGridProp {
  rootClassName?: string
}

function MainGrid({
  rootClassName
}: MainGridProp) {

  const location = useLocation()
  const [title, setTitle] = useState("")

  useEffect(() => {
    if (location.pathname == "/fixtext") {
      setTitle("固定長ファイルビューワー")
    } else if (location.pathname == "/jointext") {
      setTitle("テキストファイル結合")
    }
    
  }, [location])
  

  return (

    
      <div className={rootClassName}>
        <div className={style.grid}>
          <div className={style.side}>
            <Side />
          </div>
          <div className={style.main}>
            <h1 className={style.pageTitle}>{title}</h1>
            <div className={style.routeContent}>
              <Routes>
                <Route path={`${process.env.PUBLIC_URL}/jointext`} element={<JoinText />} />
                <Route path={`${process.env.PUBLIC_URL}/fixtext`} element={<FixTextReader />} />
                <Route path={`${process.env.PUBLIC_URL}/exif`} element={<Exif />} />
                <Route path={`${process.env.PUBLIC_URL}/test`} element={<TestPage />} />
              </Routes>
            </div>
          </div>
        </div>
      </div>
    
  )
}

export default MainGrid